
import { Options, Vue } from "vue-class-component";
import { UISelectOption } from "@/interfaces/UISelectOption";

@Options({
    name: "CustomSelect",
    props: {
        options: {
            type: Array,
            required: true
        },
        tabindex: {
            type: Number,
            required: false,
            default: 0
        },
        selectedKey: {
            type: String,
            required: true
        }
    }
})
export default class CustomSelect extends Vue {
    options!: Array<UISelectOption>;
    selectedKey!: string;
    selected?: UISelectOption = {
        key: "",
        displayValue: ""
    };
    open = false;

    created() {
        this.selected =
            this.options.find(({ key }) => key === this.selectedKey) ??
            this.options[0];
        this.$emit("update:selectedKey", this.selected?.key);
    }

    onSelectClick() {
        this.open = !this.open;
    }

    setOption(option: UISelectOption) {
        this.selected = option;
        this.open = false;
        this.$emit("update:selectedKey", this.selected?.key);
    }
}
